import React from 'react'
import { Link } from 'gatsby'
import { injectIntl, intlShape, FormattedMessage } from 'react-intl'

//import LocalizedLink from '../LocalizedLink'

import locales from '../../constants/locales'

import WattyLogo from "./images/watty-logo-cropped.png"

const Header = ({ intl: { locale } }) => (
<div class="container-inner">
    <header>
      <div class="nav-outer-wrapper">
       <div class="nav-wrapper">
        <div class="nav-wrapper-inner pad-tb">
        <div class="logo-wrapper">
          <img src={WattyLogo} alt="Logo" />
          <p class="white-font regular-txt font-med uppercase"><FormattedMessage id="logoCopy" /></p>
        </div>
        </div>
        <div class="nav-wrapper-inner">
        <div class="country-wrapper">
          <div class="country-inner">
           <div class="country-select-wrapper">
           <div class="hamburger-wrapper">
              <div class="hamburger"></div>
              <div class="hamburger"></div>
              <div class="hamburger"></div>
            </div>
            <a href="#" id="country-dd"></a>
           </div>
            <div class="country-list">
             {Object.keys(locales).map(key => (
                <Link
                 key={locales[key].locale}
                 to={locales[key].default ? '/' : `/${locales[key].path}`}
                 class=""
                 >
                {locales[key].locale}
              </Link>
              ))}
            </div>
          </div>
        </div>
       </div>
       </div>
      </div>
      <div>
      <nav >

      </nav>
    </div>
    </header>
</div>

)

Header.propTypes = { intl: intlShape.isRequired }

export default injectIntl(Header)